        <!-- <span>{{ selectedDay }}</span> -->
      </el-form-item>
      <el-form-item label="„Ç´„ÉÜ„Ç¥„É™">
        <el-select v-model="form.category" placeholder="ÈÅ∏Êäû„Åó„Å¶„Åè„Å†„Åï„ÅÑ" @change="handleCategoryChange">
          <el-option label="Â∏∏Âã§" value="c1"></el-option>
          <el-option label="„Ç≤„Çπ„Éà" value="c2"></el-option>
          <el-option label="„Çø„Ç§„Éà„É´Êà¶" value="c3"></el-option>
          <el-option label="„Éó„É©„Ç§„Éô„Éº„Éà" value="c4"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item v-if="!locationDisabled1" label="Â†¥ÊâÄ">
        <el-select v-model="form.location1" placeholder="ÈÅ∏Êäû„Åó„Å¶„Åè„Å†„Åï„ÅÑ">
          <el-option label="„Éâ„É©„Ç¥„É≥ÈõÄËçòÊ∏ãË∞∑Â∫ó" value="l1"></el-option>
          <el-option label="„Åò„ÇÉ„Çì„Åã„Å§Ê±†Ë¢ãÂ∫ó" value="l2"></el-option>
          <el-option label="È∫ªÈõÄBARÈäÄÂ∫ß" value="l3"></el-option>
          <el-option label="È∫ªÈõÄBARÊñ∞Ê©ã" value="l4"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item v-if="!locationDisabled2" label="Â†¥ÊâÄ">
        <el-input v-model="form.location2" placeholder="„Éï„É™„ÉºÂÖ•Âäõ" />
      </el-form-item>
      <el-form-item label="„É°„É¢">
        <el-input type="textarea" v-model="form.memo" />
      </el-form-item>
      <el-form-item label="ÈñãÂßãÊôÇÈñì">
        <el-time-picker v-model="form.timefrom" :picker-options="timeOptions" />
      </el-form-item>
      <el-form-item label="ÁµÇ‰∫ÜÊôÇÈñì">
        <el-time-picker v-model="form.timeto" :picker-options="timeOptions" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm">ÁôªÈå≤</el-button>
