  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        date: null,
        category: "",
        location: "",
        memo: "",
        timefrom: null,
        timeto: null,
      },
      selectedDay: "",
      locationDisabled1: true,
      locationDisabled2: true,
      rules: {
        date: [{ required: true, message: "Êó•‰ªò„ÇíÈÅ∏Êäû„Åó„Å¶„Åè„Å†„Åï„ÅÑ", trigger: "change" }],
        category: [{ required: true, message: "„Ç´„ÉÜ„Ç¥„É™„ÇíÈÅ∏Êäû„Åó„Å¶„Åè„Å†„Åï„ÅÑ", trigger: "change" }],
        location: [{ required: true, message: "Â†¥ÊâÄ„ÇíÈÅ∏Êäû„Åæ„Åü„ÅØÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ", trigger: "change" }],
        memo: [{ required: true, message: "„É°„É¢„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ", trigger: "blur" }],
        timefrom: [{ required: true, message: "ÈñãÂßãÊôÇÈñì„ÇíÈÅ∏Êäû„Åó„Å¶„Åè„Å†„Åï„ÅÑ", trigger: "change" }],
        timeto: [{ required: true, message: "ÁµÇ‰∫ÜÊôÇÈñì„ÇíÈÅ∏Êäû„Åó„Å¶„Åè„Å†„Åï„ÅÑ", trigger: "change" }],
      },
      timeOptions: {
        selectableRange: "18:00:00 - 24:00:00",
      },
    };
  },
  methods: {
    updateDay() {
      if (this.form.date) {
        const day = new Date(this.form.date).toLocaleDateString("ja-JP", { weekday: "short" });
        this.selectedDay = day;
      } else {
        this.selectedDay = "";
      }
    },
    handleCategoryChange(value) {
      this.locationDisabled1 = value === "c3" || value === "c4";
      if (this.locationDisabled1) {
        this.form.location1 = "";
      }
      this.locationDisabled2 = value === "c1" || value === "c2";
      if (this.locationDisabled2) {
        this.form.location2 = "";
      }
    },
    submitForm() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          // „Éï„Ç©„Éº„É†„ÅÆ„Éê„É™„Éá„Éº„Ç∑„Éß„É≥„ÅåÊàêÂäü„Åó„ÅüÂ†¥Âêà„ÄÅ„Åì„Åì„Åß„Éï„Ç©„Éº„É†„ÅÆÈÄÅ‰ø°Âá¶ÁêÜ„ÇíË°å„ÅÑ„Åæ„Åô
          console.log("Form submitted");
        } else {
          console.log("Form validation failed");
          return false;
        }
      });
    },
  },
};
</script>

