// scheduleData.js
const daysOfWeek = ['Êó•', 'Êúà', 'ÁÅ´', 'Ê∞¥', 'Êú®', 'Èáë', 'Âúü'];
const categories = ['Â∏∏Âã§', '„Ç≤„Çπ„Éà', '„Çø„Ç§„Éà„É´Êà¶', '„Éó„É©„Ç§„Éô„Éº„Éà', '-']; // ÊúÄÂæå„ÅÆÁ©∫ÊñáÂ≠óÂàó„ÅØ„ÄåÁ©∫ÁôΩ„Äç„ÅÆ„Ç´„ÉÜ„Ç¥„É™„ÇíË°®„Åô
const titles = [
  '„Éâ„É©„Ç¥„É≥ÈõÄËçòÊ∏ãË∞∑Â∫óüê≤',
  'È∫ªÈõÄBARÈäÄÂ∫ßüçæ',
  '„Åò„ÇÉ„Çì„Åã„Å§Ê±†Ë¢ãÂ∫óüÄÑ',
  'È∫ªÈõÄBARÊñ∞Ê©ãüç∑',
  '„Éó„É≠„ÇØ„Ç§„Éº„É≥‰∫àÈÅ∏',
  'Â•≥ÊµÅÈõÄÁéãÈÅ∏',
  '„Çπ„Éº„Éë„ÉºË≤∑„ÅÑÁâ©',
  '‰∫àÂÆö„Å™„Åó',
];
const times = [
  '15-22.5ÊôÇ',
  '18-24ÊôÇ',
  '17-23ÊôÇ',
  '18-23.5ÊôÇ',
  '',
];

const generateRandomData = (date) => {
  const day = daysOfWeek[new Date(date.getFullYear(), date.getMonth(), date.getDate()).getDay()]; // ÂΩìÊúà„ÅÆÊõúÊó•
  const category = categories[Math.floor(Math.random() * categories.length)]; // „É©„É≥„ÉÄ„É†„Å™„Ç´„ÉÜ„Ç¥„É™
  let title = '';
  let time = '';

  // category„Å´Âøú„Åò„Å¶title„Å®time„ÇíË®≠ÂÆö
  switch (category) {
    case 'Â∏∏Âã§':
      title = titles[Math.floor(Math.random() * 2)]; // „Éë„Çø„Éº„É≥1„Åã„Éë„Çø„Éº„É≥2„ÅÆ„Çø„Ç§„Éà„É´„Çí„É©„É≥„ÉÄ„É†„Å´ÈÅ∏Êäû
      time = times[Math.floor(Math.random() * 2)]; // „Éë„Çø„Éº„É≥1„Åã„Éë„Çø„Éº„É≥2„ÅÆÊôÇÈñì„Çí„É©„É≥„ÉÄ„É†„Å´ÈÅ∏Êäû
      break;
    case '„Ç≤„Çπ„Éà':
      title = titles[Math.floor(Math.random() * 2) + 2]; // „Éë„Çø„Éº„É≥3„Åã„Éë„Çø„Éº„É≥4„ÅÆ„Çø„Ç§„Éà„É´„Çí„É©„É≥„ÉÄ„É†„Å´ÈÅ∏Êäû
      time = times[Math.floor(Math.random() * 2) + 2]; // „Éë„Çø„Éº„É≥3„Åã„Éë„Çø„Éº„É≥4„ÅÆÊôÇÈñì„Çí„É©„É≥„ÉÄ„É†„Å´ÈÅ∏Êäû
      break;
    case '„Çø„Ç§„Éà„É´Êà¶':
      title = titles[Math.floor(Math.random() * 2) + 4]; // „Éë„Çø„Éº„É≥5„Åã„Éë„Çø„Éº„É≥6„ÅÆ„Çø„Ç§„Éà„É´„Çí„É©„É≥„ÉÄ„É†„Å´ÈÅ∏Êäû
      break;
    case '„Éó„É©„Ç§„Éô„Éº„Éà':
      title = '„Çπ„Éº„Éë„ÉºË≤∑„ÅÑÁâ©'; // „Éë„Çø„Éº„É≥7„ÅÆ„Çø„Ç§„Éà„É´
      time = '9-19ÊôÇ'; // „Éë„Çø„Éº„É≥7„ÅÆÊôÇÈñì
      break;
    default:
      title = '‰∫àÂÆö„Å™„Åó'; // Á©∫ÁôΩ„ÅÆ„Ç´„ÉÜ„Ç¥„É™„ÅÆÂ†¥Âêà„ÅØ"‰∫àÂÆö„Å™„Åó"„ÇíË®≠ÂÆö
  }

  return { date: date.getDate(), day, category, title, time };
};

// scheduleData„ÅÆÈÖçÂàó„ÇíÁîüÊàê
const scheduleData = [];
const currentDate = new Date();
const firstDate = new Date(currentDate.getFullYear(), currentDate.getMonth(), 1);
const lastDate = new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 0);
for (let date = firstDate; date <= lastDate; date.setDate(date.getDate() + 1)) {
  scheduleData.push(generateRandomData(date));
}

export { scheduleData };
