    </div>
  </div>
</template>

<script>
import { ref } from 'vue';
// import { useRouter } from 'vue-router';
import { ElIcon } from 'element-plus';
import { ArrowLeftBold, ArrowRightBold } from '@element-plus/icons-vue';
import { scheduleData } from '../data/scheduleData';

export default {
  setup() {
    const currentYear = ref(new Date().getFullYear());
    const currentMonth = ref(new Date().getMonth() + 1);
    const scheduleDataRef = ref(scheduleData);
    return {
      currentYear,
      currentMonth,
      scheduleData: scheduleDataRef,
    };
  },
  computed: {
    getCurrentYearMonth() {
      // ÁèæÂú®„ÅÆÂπ¥„Å®Êúà„ÇíÂèñÂæó„Åó„Å¶Ëøî„Åô
      const currentDate = new Date();
      const year = currentDate.getFullYear();
      const month = currentDate.getMonth() + 1;
      // "YYYYÂπ¥MÊúà"„ÅÆ„Éï„Ç©„Éº„Éû„ÉÉ„Éà„Å´Â§âÊèõ„Åó„Å¶Ëøî„Åô
      return `${year}Âπ¥${month}Êúà`;
    },
    prevIcon() {
      return ArrowLeftBold;
    },
    nextIcon() {
      return ArrowRightBold;
    },
    ElIcon,
  },
  methods: {
    // handleRowDblClick(date) {
    //   const router = useRouter();
    //   // title„Çí„ÇØ„Ç®„É™„Éë„É©„É°„Éº„Çø„Å®„Åó„Å¶Ê∏°„Åó„Å¶CreateSchedule.vue„Å´ÈÅ∑Áßª
    //   router.push({ name: 'CreateSchedule', query: { date } });
    // },
    getCategoryClass(category) {
      if (category === 'Â∏∏Âã§') {
        return 'category-joukin';
      } else if (category === '„Ç≤„Çπ„Éà') {
        return 'category-guest';
      } else if (category === '„Çø„Ç§„Éà„É´Êà¶') {
        return 'category-title';
      } else if (category === '„Éó„É©„Ç§„Éô„Éº„Éà') {
        return 'category-private';
      } else if (category === '-') {
        return 'category-empty';
      } else {
        return '';
      }
    }
  }
};
</script>

